@font-face {
    font-family: "Proxima Nova Cn Lt";
    src: url("../fonts/proximaNovaCondensed/ProximaNovaCond-Semibold.eot");
    src: local("Proxima Nova Condensed Semibold"),
        local("ProximaNovaCond-Semibold"),
        url("../fonts/proximaNovaCondensed/ProximaNovaCond-Semibold.eot?#iefix")
            format("embedded-opentype"),
        url("../fonts/proximaNovaCondensed/ProximaNovaCond-Semibold.woff2")
            format("woff2"),
        url("../fonts/proximaNovaCondensed/ProximaNovaCond-Semibold.woff")
            format("woff"),
        url("../fonts/proximaNovaCondensed/ProximaNovaCond-Semibold.ttf")
            format("truetype");
    font-weight: 600;
    font-style: normal;
}

